import React, {useState} from 'react'
import SuperRange from './common/c7-SuperRange/SuperRange'
import SuperDoubleRange from './common/c8-SuperDoubleRange/SuperDoubleRange'
import s from './HW11.module.css'

function HW11() {
    const [value1, setValue1] = useState(0)
    const [value2, setValue2] = useState(100)

    const onDoubleRangeChange = (value: Array<number>) => {
        setValue1(value[0])
        setValue2(value[1])
    }

    return (
        <div>
            <hr/>
            homeworks 11

            {/*should work (должно работать)*/}
            <div className={s.singleRange}>
                <span className={s.valueSpan}>{value1}</span>
                <SuperRange
                    value={value1}
                    onChangeRange={setValue1}
                    max={value2}
                    // сделать так чтоб value1 изменялось
                />
            </div>

            <div className={s.doubleRange}>
                <span className={s.valueSpan}>{value1}</span>
                <SuperDoubleRange value={[value1, value2]}
                                  onChangeRange={onDoubleRangeChange}
                    // сделать так чтоб value1 и value2 изменялось
                />
                <span className={s.valueSpan}>{value2}</span>
            </div>

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperRange/>*/}
            {/*<AlternativeSuperDoubleRange/>*/}
            <hr/>
        </div>
    )
}

export default HW11
